.ga-cta {
  &__content {
    @media (max-width: 480px) {
      max-width: 408px;
    }
  }

  &__wrapper {
    display: flex;
    @media (max-width: 480px) {
      flex-direction: column;
      gap: 40px;
    }
  }

  &__list {
    margin: 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;

    @media (max-width: 480px) {
      font-size: 20px;
      max-width: 378px;
    }

    &-item {
      margin-top: 40px;
    }
  }

  &__summary {
    font-size: 26px;
    line-height: 32px;
    & span {
      font-weight: 500;
    }
    @media (max-width: 480px) {
      margin-top: 40px;
      font-size: 20px;
      line-height: 24px;
    }
  }
}
